import './App.css';

import React from 'react';
import { Switch, Route } from 'react-router-dom';
import { Router } from 'react-router-dom';
import { Provider } from 'react-redux';
import { createStore, applyMiddleware, combineReducers } from 'redux';
import thunkMiddleware from 'redux-thunk';
import { composeWithDevTools } from 'redux-devtools-extension';
import ReactGA from 'react-ga';
import 'bulma/css/bulma.css';

import reducer from './reducers/index.reducer';

import FlashMessages from './FlashMessages';
import Navbar from './components/navbar/Navbar';
import Home from './components/home/Home';
import Search from './components/search/Search';
import AlbumsAndPlaylists from './components/albumsAndPlaylists/AlbumsAndPlaylists';
import auth from './components/auth/Auth';
import TracksTable from './components/tracks/TracksTable';
import SelectionTable from './components/userSelection/UserSelection';
import AjaxSpinner from './AjaxSpinner';
import { get_local_storage_status } from './checkStorage';
import { createBrowserHistory } from 'history';
import { TRACKING_ID } from './googleAnalyticsCreds';

get_local_storage_status();

const store = createStore(
  combineReducers(reducer),
  composeWithDevTools(
    applyMiddleware(thunkMiddleware)
    // other store enhancers if any
  )
);

ReactGA.initialize(TRACKING_ID);
const history = createBrowserHistory();

// Initialize google analytics page view tracking
history.listen(location => {
  ReactGA.set({ page: location.pathname }); // Update the user's current page
  ReactGA.pageview(location.pathname); // Record a pageview for the given page
});

const App: React.FC = () => {
  return (
    <Provider store={store}>
      <Router history={history}>
        <div>
          <AjaxSpinner />
          <FlashMessages />
          <Navbar />
          <Switch>
            <Route path="/search" component={auth(Search)}></Route>
            <Route
              path="/your-playlists"
              component={auth(
                AlbumsAndPlaylists,
                {
                  type: 'PLAYLISTS'
                },
                true
              )}
            ></Route>
            <Route
              path="/your-albums"
              component={auth(
                AlbumsAndPlaylists,
                {
                  type: 'ALBUMS'
                },
                true
              )}
            ></Route>
            <Route path="/tracks" component={auth(TracksTable)}></Route>
            <Route path="/selection" component={auth(SelectionTable)}></Route>
            <Route path="/" component={auth(Home)}></Route>
          </Switch>
        </div>
      </Router>
    </Provider>
  );
};

export default App;
