import React from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router';
import { compose } from 'redux';
import axios from 'axios';
import { v4 as uuidv4 } from 'uuid';
import ReactGA from 'react-ga';
import { GlobalState } from '../../reducers/index.reducer';

import {
  ADD_PENDING_REQUEST,
  FINISH_PENDING_REQUEST,
  AUTHENTICATE,
  ADD_FLASH_MESSAGE
} from '../../actions/index.actions';
import {
  API_ROOT,
  HTTP_ORIGIN,
  AJAX_REQUEST_TYPES,
  FLASH_MESSAGE_TYPES
} from '../../constants/constants';

import { login } from './login';

interface AuthenticateProps {
  authenticate: Function;
  isAuthenticated: boolean;
  history: any;
  addPendingRequest: Function;
  finishPendingRequest: Function;
  requireAuth: boolean;
  addFlashMessage: Function;
}

interface AuthenticateState {
  displayName: string;
}

export default function(
  ComposedComponent: any,
  extraProps?: any,
  authRequired?: boolean
): any {
  class Authenticate extends React.Component<
    AuthenticateProps,
    AuthenticateState
  > {
    constructor(props: AuthenticateProps) {
      super(props);
      this.state = {
        displayName: ''
      };
    }
    componentDidMount() {
      const code = new URLSearchParams(window.location.search).get('code');
      if (code) {
        this.authenticateUser();
        return;
      }
      this.checkAndRedirect();
    }

    componentDidUpdate() {
      this.checkAndRedirect();
    }

    login() {
      const {
        addPendingRequest,
        finishPendingRequest,
        addFlashMessage
      } = this.props;
      login(addPendingRequest, finishPendingRequest, addFlashMessage);
    }

    async authenticateUser() {
      const requestId = uuidv4();
      try {
        this.props.addPendingRequest({
          id: requestId,
          type: AJAX_REQUEST_TYPES.PROFILE,
          message: 'Authenticating'
        });
        await axios.get(
          `${API_ROOT}/authorize?code=${new URLSearchParams(
            window.location.search
          ).get('code')}`,
          {
            withCredentials: true
          }
        );

        this.checkAndRedirect();

        this.props.finishPendingRequest(requestId);
      } catch (error) {
        this.props.addFlashMessage({
          type: FLASH_MESSAGE_TYPES.ERROR,
          text: 'Error Authenticating',
          id: uuidv4()
        });
        this.props.finishPendingRequest(requestId);
      }
    }

    async checkAndRedirect() {
      const { isAuthenticated } = this.props;
      if (isAuthenticated) return;
      const requestId = uuidv4();

      try {
        this.props.addPendingRequest({
          id: requestId,
          message: 'Fetching Profile',
          type: AJAX_REQUEST_TYPES.PROFILE
        });

        const profile = await axios.post(
          `${API_ROOT}/spotify-request`,
          {
            spotify_path: 'https://api.spotify.com/v1/me',
            spotify_payload: null,
            crud_operation: 'get'
          },
          {
            withCredentials: true,
            headers: { 'Access-Control-Allow-Origin': HTTP_ORIGIN }
          }
        );

        this.props.finishPendingRequest(requestId);

        if (profile.data && profile.data.id) {
          this.props.authenticate(profile.data.id);

          localStorage.setItem('logged_in_tonalify', profile.data.display_name);
          this.setState({ displayName: profile.data.display_name });
          return;
        }

        if (authRequired) {
          this.props.history.push('/home');
        }
      } catch (error) {
        this.props.finishPendingRequest(requestId);
      }
    }

    render() {
      return this.props.requireAuth ? (
        this.props.isAuthenticated ? (
          <ComposedComponent
            {...(this.props, extraProps)}
            displayName={this.state.displayName}
            login={() => this.login()}
          />
        ) : null
      ) : (
        <ComposedComponent
          {...(this.props, extraProps)}
          login={() => this.login()}
        />
      );
    }
  }

  const mapDispatchToProps = (dispatch: any) => {
    return {
      authenticate: (userId: string) =>
        dispatch({ type: AUTHENTICATE, payload: userId }),
      addPendingRequest: (request: any) =>
        dispatch({ type: ADD_PENDING_REQUEST, payload: request }),
      finishPendingRequest: (id: any) =>
        dispatch({ type: FINISH_PENDING_REQUEST, payload: id }),
      addFlashMessage: (message: any) =>
        dispatch({ type: ADD_FLASH_MESSAGE, payload: message })
    };
  };

  const mapStateToProps = (state: GlobalState) => {
    return {
      isAuthenticated: state.auth.isAuthenticated
    };
  };

  return compose(
    withRouter,
    connect(mapStateToProps, mapDispatchToProps)
  )(Authenticate);
}
