import axios from 'axios';
import { v4 as uuidv4 } from 'uuid';
import {
  API_ROOT,
  HTTP_ORIGIN,
  AJAX_REQUEST_TYPES,
  FLASH_MESSAGE_TYPES
} from '../../constants/constants';

export const login = async (
  addPendingRequest: Function,
  finishPendingRequest: Function,
  addFlashMessage: Function
) => {
  const requestId = uuidv4();

  try {
    addPendingRequest({
      id: requestId,
      type: AJAX_REQUEST_TYPES.PROFILE,
      message: 'Logging In'
    });
    const loginUri: { data: string } = await axios.get(`${API_ROOT}/login`);
    finishPendingRequest(requestId);
    if (loginUri && loginUri.data) {
      window.location.href = loginUri.data;
      return;
    }
    addFlashMessage({
      type: FLASH_MESSAGE_TYPES.ERROR,
      text: 'Error Fetching Login URL',
      id: uuidv4()
    });
  } catch (error) {
    finishPendingRequest(requestId);
    addFlashMessage({
      type: FLASH_MESSAGE_TYPES.ERROR,
      text: 'Error Fetching Login URL',
      id: uuidv4()
    });
  }
};
